Cd;IACA;QACI,wBAAwB;QACxB,UAAU;IACd;AACJ;;AAEA,UAAU;AACV;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,2BAA2B;IAC3B,aAAa;IACb,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;AACjB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;IAClB,aAAa;IACb,gBAAgB;IAChB,UAAU;IACV,gBAAgB;IAChB,gBAAgB;AACpB;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,WAAW;AACf;;AAEA;IACI,aAAa;IACb,SAAS;IACT,yBAAyB;IACzB,gBAAgB;AACpB;;AAEA,kBAAkB;AAClB;IACI,iBAAiB;IACjB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,SAAS;IACT,mBAAmB;AACvB;;AAEA;IACI,mBAAmB;IACnB,uBAAuB;IACvB,kBAAkB;IAClB,aAAa;IACb,kBAAkB;IAClB,eAAe;IACf,WAAW;IACX,yBAAyB;AAC7B;;AAEA;IACI,mBAAmB;IACnB,qBAAqB;IACrB,cAAc;AAClB;;AAEA,mBAAmB;AACnB;IACI,iBAAiB;IACjB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,eAAe;IACf,gCAAgC;AACpC;;AAEA;IACI,mBAAmB;IACnB,mBAAmB;IACnB,WAAW;IACX,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,mBAAmB;IACnB,YAAY;IACZ,2BAA2B;AAC/B;;AAEA,sBAAsB;AACtB;IACI;QACI,sBAAsB;IAC1B;;IAEA;QACI,WAAW;QACX,aAAa;QACb,kBAAkB;QAClB,gCAAgC;IACpC;;IAEA;QACI,cAAc;IAClB;;IAEA;QACI,YAAY;QACZ,WAAW;IACf;AACJ;;AAEA,iBAAiB;AACjB;IACI,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;IACb,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,cAAc;IACd,mBAAmB;AACvB;;AAEA;IACI,WAAW;IACX,mBAAmB;IACnB,gBAAgB;AACpB;;AAEA,oBAAoB;AACpB,UAAU,aAAa,EAAE;AACzB,QAAQ,aAAa,EAAE;AACvB,eAAe,sBAAsB,EAAE;AACvC,eAAe,mBAAmB,EAAE,uBAAuB,EAAE;AAC7D,eAAe,kBAAkB,EAAE;AACnC,cAAc,iBAAiB,EAAE;AACjC,SAAS,mBAAmB,EAAE;AAC9B,SAAS,mBAAmB,EAAE;AAC9B,QAAQ,aAAa,EAAE;AACvB,QAAQ,aAAa,EAAE","sourcesContent":["/* Reset and Base Styles */\r\n* {\r\n    box-sizing: border-box;\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\nbody {\r\n    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;\r\n    line-height: 1.6;\r\n    color: #333;\r\n    background-color: #f5f5f5;\r\n}\r\n\r\n/* Loading Screen */\r\n.loading {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n    height: 100vh;\r\n    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);\r\n    color: white;\r\n}\r\n\r\n.spinner {\r\n    width: 50px;\r\n    height: 50px;\r\n    border: 4px solid rgba(255, 255, 255, 0.3);\r\n    border-left: 4px solid white;\r\n    border-radius: 50%;\r\n    animation: spin 1s linear infinite;\r\n    margin-bottom: 20px;\r\n}\r\n\r\n@keyframes spin {\r\n    0% { transform: rotate(0deg); }\r\n    100% { transform: rotate(360deg); }\r\n}\r\n\r\n/* Main App Layout */\r\n.app-container {\r\n    display: flex;\r\n    height: 100vh;\r\n    overflow: hidden;\r\n}\r\n\r\n.sidebar {\r\n    width: 300px;\r\n    background: white;\r\n    border-right: 1px solid #e0e0e0;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.main-content {\r\n    flex: 1;\r\n    display: flex;\r\n    flex-direction: column;\r\n    background: #fafafa;\r\n}\r\n\r\n/* Header */\r\n.header {\r\n    background: white;\r\n    padding: 15px 20px;\r\n    border-bottom: 1px solid #e0e0e0;\r\n    display: flex;\r\n    justify-content: between;\r\n    align-items: center;\r\n}\r\n\r\n.user-info {\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 10px;\r\n}\r\n\r\n.user-avatar {\r\n    width: 40px;\r\n    height: 40px;\r\n    border-radius: 50%;\r\n    background: #667eea;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    color: white;\r\n    font-weight: bold;\r\n}\r\n\r\n.chatbot-overlay {\r\n    position: absolute;\r\n    bottom: -2px;\r\n    right: -2px;\r\n    width: 16px;\r\n    height: 16px;\r\n    background: #4CAF50;\r\n    border-radius: 50%;\r\n    border: 2px solid white;\r\n}\r\n\r\n/* Chatroom Info */\r\n.chatroom-info {\r\n    padding: 15px 20px;\r\n    background: #f8f9fa;\r\n    border-bottom: 1px solid #e0e0e0;\r\n}\r\n\r\n.chatroom-title {\r\n    font-weight: bold;\r\n    margin-bottom: 5px;\r\n}\r\n\r\n.chatroom-status {\r\n    font-size: 0.9em;\r\n    color: #666;\r\n}\r\n\r\n/* Conversation List */\r\n.conversation-list {\r\n    flex: 1;\r\n    overflow-y: auto;\r\n    padding: 10px;\r\n}\r\n\r\n.conversation-item {\r\n    background: white;\r\n    border-radius: 8px;\r\n    padding: 15px;\r\n    margin-bottom: 10px;\r\n    border: 1px solid #e0e0e0;\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.conversation-item:hover {\r\n    box-shadow: 0 2px 8px rgba(0,0,0,0.1);\r\n    transform: translateY(-1px);\r\n}\r\n\r\n.conversation-item.active {\r\n    border-color: #667eea;\r\n    background: #f8f9ff;\r\n}\r\n\r\n.conversation-preview {\r\n    display: flex;\r\n    justify-content: space-between;\r\n    align-items: flex-start;\r\n}\r\n\r\n.conversation-details h4 {\r\n    margin-bottom: 5px;\r\n    color: #333;\r\n}\r\n\r\n.conversation-details p {\r\n    color: #666;\r\n    font-size: 0.9em;\r\n}\r\n\r\n.conversation-meta {\r\n    text-align: right;\r\n    font-size: 0.8em;\r\n    color: #999;\r\n}\r\n\r\n.unread-badge {\r\n    background: #667eea;\r\n    color: white;\r\n    border-radius: 50%;\r\n    width: 20px;\r\n    height: 20px;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    font-size: 0.8em;\r\n    margin-top: 5px;\r\n}\r\n\r\n/* Chat Area */\r\n.chat-area {\r\n    flex: 1;\r\n    display: flex;\r\n    flex-direction: column;\r\n}\r\n\r\n.messages-container {\r\n    flex: 1;\r\n    overflow-y: auto;\r\n    padding: 20px;\r\n    background: #fafafa;\r\n}\r\n\r\n.message {\r\n    max-width: 70%;\r\n    margin-bottom: 15px;\r\n    display: flex;\r\n    align-items: flex-start;\r\n    gap: 10px;\r\n}\r\n\r\n.message.sent {\r\n    margin-left: auto;\r\n    flex-direction: row-reverse;\r\n}\r\n\r\n.message-bubble {\r\n    background: white;\r\n    padding: 12px 16px;\r\n    border-radius: 18px;\r\n    box-shadow: 0 1px 2px rgba(0,0,0,0.1);\r\n    position: relative;\r\n}\r\n\r\n.message.sent .message-bubble {\r\n    background: #667eea;\r\n    color: white;\r\n}\r\n\r\n.message-time {\r\n    font-size: 0.7em;\r\n    color: #999;\r\n    margin-top: 5px;\r\n}\r\n\r\n/* Talk Interface */\r\n.talk-question {\r\n    background: white;\r\n    border-radius: 12px;\r\n    padding: 20px;\r\n    margin-bottom: 20px;\r\n    border: 2px solid #e0e0e0;\r\n}\r\n\r\n.question-text {\r\n    font-size: 1.1em;\r\n    margin-bottom: 15px;\r\n    color: #333;\r\n}\r\n\r\n.answer-options {\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 10px;\r\n}\r\n\r\n.answer-chip {\r\n    background: #f0f0f0;\r\n    border: 2px solid transparent;\r\n    border-radius: 20px;\r\n    padding: 10px 16px;\r\n    cursor: pointer;\r\n    transition: all 0.2s ease;\r\n    text-align: center;\r\n}\r\n\r\n.answer-chip:hover {\r\n    background: #e8f0fe;\r\n    border-color: #667eea;\r\n}\r\n\r\n.answer-chip.selected {\r\n    background: #667eea;\r\n    color: white;\r\n    border-color: #5a67d8;\r\n}\r\n\r\n.answer-chip.ignore {\r\n    background: #f56565;\r\n    color: white;\r\n}\r\n\r\n.answer-chip.match {\r\n    background: #48bb78;\r\n    color: white;\r\n}\r\n\r\n/* Message Input */\r\n.message-input-area {\r\n    background: white;\r\n    border-top: 1px solid #e0e0e0;\r\n    padding: 15px 20px;\r\n}\r\n\r\n.message-input-container {\r\n    display: flex;\r\n    gap: 10px;\r\n    align-items: flex-end;\r\n}\r\n\r\n.message-input {\r\n    flex: 1;\r\n    border: 1px solid #e0e0e0;\r\n    border-radius: 20px;\r\n    padding: 10px 15px;\r\n    outline: none;\r\n    resize: none;\r\n    min-height: 40px;\r\n    max-height: 120px;\r\n}\r\n\r\n.message-input:focus {\r\n    border-color: #667eea;\r\n}\r\n\r\n.send-button {\r\n    background: #667eea;\r\n    color: white;\r\n    border: none;\r\n    border-radius: 50%;\r\n    width: 40px;\r\n    height: 40px;\r\n    cursor: pointer;\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: center;\r\n    transition: background 0.2s ease;\r\n}\r\n\r\n.send-button:hover {\r\n    background: #5a67d8;\r\n}\r\n\r\n.send-button:disabled {\r\n    background: #ccc;\r\n    cursor: not-allowed;\r\n}\r\n\r\n/* Tabs */\r\n.tabs {\r\n    display: flex;\r\n    background: white;\r\n    border-bottom: 1px solid #e0e0e0;\r\n}\r\n\r\n.tab {\r\n    flex: 1;\r\n    padding: 15px;\r\n    text-align: center;\r\n    cursor: pointer;\r\n    border-bottom: 3px solid transparent;\r\n    transition: all 0.2s ease;\r\n}\r\n\r\n.tab:hover {\r\n    background: #f8f9fa;\r\n}\r\n\r\n.tab.active {\r\n    border-bottom-color: #667eea;\r\n    color: #667eea;\r\n    font-weight: bold;\r\n}\r\n\r\n/* Buttons */\r\n.btn {\r\n    background: #667eea;\r\n    color: white;\r\n    border: none;\r\n    padding: 10px 20px;\r\n    border-radius: 6px;\r\n    cursor: pointer;\r\n    font-size: 1em;\r\n    transition: all 0.2s ease;\r\n    display: inline-flex;\r\n    align-items: center;\r\n    gap: 8px;\r\n}\r\n\r\n.btn:hover {\r\n    background: #5a67d8;\r\n    transform: translateY(-1px);\r\n}\r\n\r\n.btn:disabled {\r\n    background: #ccc;\r\n    cursor: not-allowed;\r\n    transform: none;\r\n}\r\n\r\n.btn-secondary {\r\n    background: #6c757d;\r\n}\r\n\r\n.btn-secondary:hover {\r\n    background: #5a6268;\r\n}\r\n\r\n.btn-success {\r\n    background: #28a745;\r\n}\r\n\r\n.btn-success:hover {\r\n    background: #218838;\r\n}\r\n\r\n.btn-danger {\r\n    background: #dc3545;\r\n}\r\n\r\n.btn-danger:hover {\r\n    background: #c82333;\r\n}\r\n\r\n/* Form Elements */\r\n.form-group {\r\n    margin-bottom: 20px;\r\n}\r\n\r\n.form-label {\r\n    display: block;\r\n    margin-bottom: 5px;\r\n    font-weight: 500;\r\n    color: #333;\r\n}\r\n\r\n.form-input {\r\n    width: 100%;\r\n    padding: 10px 15px;\r\n    border: 1px solid #e0e0e0;\r\n    border-radius: 6px;\r\n    outline: none;\r\n    transition: border-color 0.2s ease;\r\n}\r\n\r\n.form-input:focus {\r\n    border-color: #667eea;\r\n}\r\n\r\n.form-textarea {\r\n    resize: vertical;\r\n    min-height: 80px;\r\n}\r\n\r\n/* Notifications */\r\n.notifi